'use client';

import { useEffect, useRef, useState } from 'react';
import 'leaflet/dist/leaflet.css';

interface MapPin {
  id: number;
  lat: number;
  lng: number;
  category: string;
  severity: 'Low' | 'Medium' | 'High';
  note?: string;
}

const severityColors = {
  Low: '#16a34a',
  Medium: '#f59e0b',
  High: '#ef4444',
};

interface LiveMapProps {
  pins: MapPin[];
  onPinClick?: (pin: MapPin) => void;
}

export default function LiveMap({ pins, onPinClick }: LiveMapProps) {
  const mapRef = useRef<any>(null);
  const mapInstanceRef = useRef<any>(null);
  const leafletRef = useRef<any>(null);
  const userMarkerRef = useRef<any>(null);
  const [userLocation, setUserLocation] = useState<[number, number] | null>(null);
  const [isLocating, setIsLocating] = useState(false);

  // Default to Bangalore, India (where the app is set)
  const defaultCenter: [number, number] = [12.9716, 77.5946];
  const defaultZoom = 13;

  const centerOnUserLocation = () => {
    setIsLocating(true);
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const location: [number, number] = [position.coords.latitude, position.coords.longitude];
          console.log('Got location:', location);
          setUserLocation(location);
          
          if (mapInstanceRef.current) {
            mapInstanceRef.current.setView(location, 15);
          }
          setIsLocating(false);
        },
        (error) => {
          console.error('Geolocation error:', error);
          setIsLocating(false);
          alert('Unable to get your location. Make sure you granted permission and are on HTTPS or localhost.');
        },
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 0,
        }
      );
    } else {
      alert('Geolocation is not supported by your browser');
      setIsLocating(false);
    }
  };

  // Request geolocation on mount
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const location: [number, number] = [position.coords.latitude, position.coords.longitude];
          console.log('Initial location acquired:', location);
          setUserLocation(location);
        },
        (error) => {
          console.log('Geolocation permission not granted on mount:', error.code);
        },
        {
          enableHighAccuracy: false,
          timeout: 5000,
          maximumAge: 3600000, // 1 hour cache
        }
      );
    }
  }, []);

  // Store marker references
  const markersRef = useRef<any[]>([]);

  // Initialize map on mount
  useEffect(() => {
    if (typeof window === 'undefined' || !mapRef.current || mapInstanceRef.current) return;

    import('leaflet').then((L) => {
      const leaflet = L.default;
      leafletRef.current = leaflet;

      try {
        const mapInstance = leaflet.map(mapRef.current).setView(defaultCenter, defaultZoom);
        mapInstanceRef.current = mapInstance;

        leaflet
          .tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
            attribution:
              '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
            maxZoom: 19,
          })
          .addTo(mapInstance);

        // Add markers for reports
        pins.forEach((pin) => {
          const color = severityColors[pin.severity];
          const marker = leaflet
            .marker([pin.lat, pin.lng], {
              icon: leaflet.divIcon({
                className: 'custom-marker',
                html: `
              <div style="
                background-color: ${color};
                border: 3px solid white;
                border-radius: 50%;
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                box-shadow: 0 2px 8px rgba(0,0,0,0.3);
                font-weight: bold;
                font-size: 14px;
                color: white;
                cursor: pointer;
              ">
                ${pin.severity[0]}
              </div>
            `,
                iconSize: [32, 32],
                iconAnchor: [16, 16],
                popupAnchor: [0, -16],
              }),
            })
            .bindPopup(`
          <div style="min-width: 150px;">
            <p style="font-weight: bold; margin: 0 0 5px 0;">Case #${pin.id}</p>
            <p style="font-size: 12px; color: #666; margin: 0 0 5px 0;">${pin.category}</p>
            <span style="
              display: inline-block;
              font-size: 12px;
              font-weight: bold;
              border-radius: 4px;
              padding: 4px 8px;
              background-color: ${pin.severity === 'High' ? '#fca5a5' : pin.severity === 'Medium' ? '#fcd34d' : '#c6f6d5'};
              color: ${pin.severity === 'High' ? '#7f1d1d' : pin.severity === 'Medium' ? '#78350f' : '#15803d'};
            ">${pin.severity} Severity</span>
            ${pin.note ? `<p style="font-size: 12px; color: #333; margin: 8px 0 0 0;">${pin.note}</p>` : ''}
          </div>
        `)
            .on('click', () => {
              onPinClick?.(pin);
            });

          marker.addTo(mapInstance);
        });

        setTimeout(() => {
          mapInstance.invalidateSize();
        }, 100);
      } catch (error) {
        console.error('Error initializing map:', error);
      }
    });

    return () => {
      if (mapInstanceRef.current) {
        mapInstanceRef.current.remove();
        mapInstanceRef.current = null;
      }
    };
  }, []);

  // Handle pin updates
  useEffect(() => {
    if (!mapInstanceRef.current || !leafletRef.current) return;

    // Remove old markers
    markersRef.current.forEach(marker => {
      mapInstanceRef.current.removeLayer(marker);
    });
    markersRef.current = [];

    console.log('[LiveMap] Adding', pins.length, 'pins to map');

    // Add new markers
    pins.forEach((pin) => {
      const color = severityColors[pin.severity];
      const marker = leafletRef.current
        .marker([pin.lat, pin.lng], {
          icon: leafletRef.current.divIcon({
            className: 'custom-marker',
            html: `
              <div style="
                background-color: ${color};
                border: 3px solid white;
                border-radius: 50%;
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                box-shadow: 0 2px 8px rgba(0,0,0,0.3);
                font-weight: bold;
                font-size: 14px;
                color: white;
                cursor: pointer;
              ">
                ${pin.severity[0]}
              </div>
            `,
            iconSize: [32, 32],
            iconAnchor: [16, 16],
            popupAnchor: [0, -16],
          }),
        })
        .bindPopup(`
          <div style="min-width: 150px;">
            <p style="font-weight: bold; margin: 0 0 5px 0;">Case #${pin.id}</p>
            <p style="font-size: 12px; color: #666; margin: 0 0 5px 0;">${pin.category}</p>
            <span style="
              display: inline-block;
              font-size: 12px;
              font-weight: bold;
              border-radius: 4px;
              padding: 4px 8px;
              background-color: ${pin.severity === 'High' ? '#fca5a5' : pin.severity === 'Medium' ? '#fcd34d' : '#c6f6d5'};
              color: ${pin.severity === 'High' ? '#7f1d1d' : pin.severity === 'Medium' ? '#78350f' : '#15803d'};
            ">${pin.severity} Severity</span>
            ${pin.note ? `<p style="font-size: 12px; color: #333; margin: 8px 0 0 0;">${pin.note}</p>` : ''}
          </div>
        `)
        .on('click', () => {
          onPinClick?.(pin);
        });

      marker.addTo(mapInstanceRef.current);
      markersRef.current.push(marker);
    });
  }, [pins, onPinClick]);

  // Handle user location marker updates
  useEffect(() => {
    if (!mapInstanceRef.current || !leafletRef.current) return;

    // Remove old marker if it exists
    if (userMarkerRef.current) {
      mapInstanceRef.current.removeLayer(userMarkerRef.current);
      userMarkerRef.current = null;
    }

    // Add new marker if location is available
    if (userLocation) {
      const marker = leafletRef.current
        .marker(userLocation, {
          icon: leafletRef.current.icon({
            iconUrl:
              'data:image/svg+xml;base64,',
            iconSize: [32, 32],
            iconAnchor: [16, 16],
            popupAnchor: [0, -16],
          }),
        })
        .bindPopup('üìç Your Location')
        .addTo(mapInstanceRef.current);

      userMarkerRef.current = marker;
    }
  }, [userLocation]);

  return (
    <div className="absolute inset-0 w-full h-full relative" style={{ contain: 'layout style paint' }}>
      <div
        ref={mapRef}
        className="absolute inset-0 w-full h-full bg-blue-100"
      />

      {/* Legend */}
      <div className="absolute bottom-4 left-4 bg-white rounded-lg border border-gray-200 shadow-lg p-3 max-w-xs z-[500]">
        <h3 className="text-xs font-semibold mb-2 text-gray-900">Legend</h3>
        <div className="space-y-1">
          <div className="flex items-center gap-2 text-xs">
            <div
              className="w-4 h-4 rounded-full"
              style={{
                background: 'radial-gradient(circle, #3399ff 0%, #3399ff 60%, rgba(51,153,255,0.3) 100%)',
              }}
            />
            <span className="text-gray-700">Your Location</span>
          </div>
          {Object.entries(severityColors).map(([severity, color]) => (
            <div key={severity} className="flex items-center gap-2 text-xs">
              <div
                className="w-4 h-4 rounded-full border-2 border-white"
                style={{ backgroundColor: color }}
              />
              <span className="text-gray-700">{severity} Severity</span>
            </div>
          ))}
        </div>
        <p className="text-xs text-gray-500 mt-2">
          {pins.length} report{pins.length !== 1 ? 's' : ''} in this area
        </p>
      </div>

      {/* Center on Location Button */}
      <button
        onClick={centerOnUserLocation}
        disabled={isLocating}
        className="absolute bottom-4 right-4 bg-white rounded-lg border border-gray-200 shadow-lg p-3 hover:bg-gray-50 disabled:opacity-50 z-[500] transition-all"
        title="Center map on your location"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="20"
          height="20"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          className="text-gray-700"
        >
          <circle cx="12" cy="12" r="1" fill="currentColor" />
          <path d="M12 8a4 4 0 0 0-4 4" />
          <path d="M16 12a4 4 0 0 0-4-4" />
          <path d="M12 3v2m4.22 1.22.707.707M19 5v4m-1.22 4.22.707-.707M21 12h-2m-1.22 4.22-.707.707M15 19v2m-4.22 1.22-.707.707M5 19h-4m1.22-4.22-.707.707M3 12h2m1.22-4.22.707.707M9 5V3M8 12.78l-.707-.707M5 12h4" />
        </svg>
        {isLocating && <span className="ml-2 text-xs">Locating...</span>}
      </button>
    </div>
  );
}
